.set-start {
  background-color: crimson;
  border-radius: 10px;
  color: white;
}

input::placeholder {
  color: lightgray;
  font-size: 20px;
  text-align: center;
}
.set-option {
  display: flex;
  margin: 30px auto 0;
  justify-content: center;
}

.set-option input[type="number"] {
  width: 9%;
  height: 30px;
  font-size: 22px;
  margin-right: 22px;
  /* padding: 0 10px; */
  text-align: center;
  border: 0;
  /* text-indent: 17px; */
  border-bottom: 4px solid #000;
  border-radius: 3px;
}

.set-option button {
  width: 12%;
  font-size: 17px;
  font-weight: bold;
}
